@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  --color-one :#4f338f;
  --color-two :#aa7dff;
  --color-three :#e5d8ff;
  --color-four :#efebff;
  --font-size-one: 1.5rem;
  --font-size-two: 2rem;
  --font-size-three: 3rem;
  --font-size-four: 4rem;
  color: var(--color-four);
  margin: 0px;

  font-family:'Dosis', sans-serif;
}
nav{
    width: auto;
    height: 7rem;
    background-color: var(--color-three);
    display: grid;
    grid-template-rows: 30% 40% 30%;
    grid-template-columns: 5% 10% 65% 15% 5%;
}
.nav-logo{
    grid-column: 2/3;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 2rem;

    width: 1.5em;
    height: 1.5em;
    background-color: var(--color-one);
    border-radius: 30px;

    
}
.nav-contactMe{
    height: 100%;
    grid-column: 4/5;
    grid-row: 2/3;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-three);
    font-weight: 700;
    background-color: var(--color-one);
    border-radius: 5px;
    font-size: 2rem;
    text-decoration: none;
}

.socialMedia{
    display: flex;
    justify-content: center;
    align-items: center;
}

.socialMedia-logos{
    width: 6em;
}
.socialMedia-logos_github{
    width: 4em;
}
.footer{
    display: flex;
    padding: 3em 0em 0em;
    background-color: var(--color-three );
    text-align: center;
    justify-content: center;
    align-items: center;
}
.footer-socialMedia p{
    color:var(--color-one);
    font-size: var(--font-size-two);
}
.aboutMe{
    padding:8em 7em;
    background-color: var(--color-one);
    text-align: center;    
    font-size: var(--font-size-one);
}
.aboutMe-title{
    margin-bottom: 1em;
    font-size: var(--font-size-three);
    
}
.presentation{
    background-image: url(/portfolio/static/media/freelancer.1dc1e34f.svg);
    height: 35em;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: 30em;
    position: relative;
    display: grid;
    grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10% ;
    grid-template-columns: 10% 20% 20%  20% 20% 10%;
}

.presentation-helloWorld{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    grid-row: 2/4 ;
    grid-column: 3/5;
    color: var(--color-one);
}
.presentation-about{
    font-size: 1.5rem;
    color: var(--color-one);
    grid-row: 4/5 ;
    grid-column:2/6;
    display: flex;
    /* border: solid 1px red; */
    justify-content: center;
    align-items: center;
}

.presentation-contactMe{
    grid-row: 9/11 ;
    grid-column: 4/5 ;
    background-color: var(--color-three);
    border-radius: 5px;   
    display: flex;
    justify-content: center;
    align-items: center ;
    flex-direction: column;
    -webkit-transition: .5s;
    transition: .5s;
}
.presentation-contactMe:hover{
    border: 4px solid var(--color-two);
}
.presentation-contactMe p {
    color: var(--color-one);
    font-size: 1.5em;
}

.presentation-contactMe_gitTwitLinkendin{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.presentation-contactMe a img{
    width: 5em;
    /* height: 3em; */
}
.presentation-contactMe a img{
    display: flex;
    justify-content: center;
    align-items: column;
}
.presentation-contactMe_github{
    width: 3em !important;
}
.skills{
    background-color: var(--color-three);
    color: var(--color-one);
    margin: -6em 15em 2em ;
    padding: 2em 3em;
    border-radius: 25px;
    display: grid;
    grid-template-columns: repeat(3,1fr) ;    
    
}
.skills div{
    color: var(--color-one);        
    font-size: var(--font-size-two) ;
}

.listSkills{
    margin-top: 1em;
}

.listSkills li{
    color: var(--color-one);        
    font-size: var(--font-size-one) ;
}

.skills-frontend{
    font-size: 3em;
    border-radius: 25px 0px 0px 25px;
}

.skills-backend{
    grid-column: 2/4;
    border-radius: 0px 25px 25px 0px;
}

/* Images (size) */
.image-logo{
    width: 3em;
}

