.presentation{
    background-image: url("../../images/freelancer.svg");
    height: 35em;
    background-position: bottom;
    background-repeat: no-repeat;
    background-size: 30em;
    position: relative;
    display: grid;
    grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10% ;
    grid-template-columns: 10% 20% 20%  20% 20% 10%;
}

.presentation-helloWorld{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
    grid-row: 2/4 ;
    grid-column: 3/5;
    color: var(--color-one);
}
.presentation-about{
    font-size: 1.5rem;
    color: var(--color-one);
    grid-row: 4/5 ;
    grid-column:2/6;
    display: flex;
    /* border: solid 1px red; */
    justify-content: center;
    align-items: center;
}

.presentation-contactMe{
    grid-row: 9/11 ;
    grid-column: 4/5 ;
    background-color: var(--color-three);
    border-radius: 5px;   
    display: flex;
    justify-content: center;
    align-items: center ;
    flex-direction: column;
    transition: .5s;
}
.presentation-contactMe:hover{
    border: 4px solid var(--color-two);
}
.presentation-contactMe p {
    color: var(--color-one);
    font-size: 1.5em;
}

.presentation-contactMe_gitTwitLinkendin{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.presentation-contactMe a img{
    width: 5em;
    /* height: 3em; */
}
.presentation-contactMe a img{
    display: flex;
    justify-content: center;
    align-items: column;
}
.presentation-contactMe_github{
    width: 3em !important;
}